.tile-view {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  border-radius: 8%;
}

.tile-view, .tile-legend.plain {
  background-color: #EEE;
}

.tile-view.blocked, .tile-legend.blocked {
  background-color: #AAA;
}

.tile-view.focused {
  border: 0.3rem solid #982649;
}

.tile-view.in-open-list, .tile-legend.in-open-list {
  background-color: #9CD8C8;
}

.tile-view.in-closed-list, .tile-legend.in-closed-list {
  background-color: #7EB2DD;
}

.tile-view > .tile-label {
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1; /* Bring labels above the path view */
}

.tile-view > .start, .tile-view > .end {
  color: black;
}

.tile-view > .weight {
  color: rgba(0, 0, 0, 0.5);
}


